___CSS_LOADER_EXPORT___.push([module.id, `.studying {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2.8rem;
  background-color: var(--dark-color);
  border-radius: 10px;
  padding: 60px 18px;
}
@media (min-width: 640px) {
  .studying {
    padding: 80px 40px;
  }
}
@media (min-width: 1200px) {
  .studying {
    padding: 80px 104px;
  }
}
.studying__title {
  font-size: 2rem;
  text-align: center;
  max-width: 340px;
  margin: 0;
}
@media (min-width: 640px) {
  .studying__title {
    font-size: 3.45rem;
    max-width: 340px;
  }
}
@media (min-width: 1200px) {
  .studying__title {
    max-width: 100%;
  }
}
.studying__list {
  position: relative;
  display: flex;
  flex-direction: column;
  gap: 2.8rem;
}
.studying__list::before {
  content: "";
  position: absolute;
  left: 22px;
  top: 5%;
  border: 1px solid var(--accent-color);
  height: 80%;
  transform: translate(-50%);
  z-index: 1;
}
@media (min-width: 640px) {
  .studying__list::before {
    display: none;
  }
}
@media (min-width: 1200px) {
  .studying__list::before {
    display: block;
    width: 80%;
    height: 0%;
    left: 100px;
    top: 60px;
    transform: translate(0%);
  }
}
@media (min-width: 1200px) {
  .studying__list {
    flex-direction: row;
  }
