  position: relative;
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
  background-size: cover;
  background-repeat: no-repeat;
}
.learning__image {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  object-fit: cover;
  z-index: -1;
}
.learning__content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 100px 21px;
}
@media (min-width: 1200px) {
  .learning__content {
    flex-direction: row;
    justify-content: space-between;
    padding: 89px 106px;
    margin: 0 auto;
    max-width: 1200px;
  }
}
.learning__knowledge {
  display: flex;
  flex-direction: column;
  gap: 1.45rem;
  max-width: 460px;
}
.learning__title {
  font-size: 2rem;
  text-align: center;
  margin-top: 24px;
}
@media (min-width: 640px) {
  .learning__title {
    font-size: 4rem;
  }
}
@media (min-width: 1200px) {
  .learning__title {
    text-align: start;
  }
}
.learning__program-link {
  font-size: 1.15rem;
  text-align: center;
}
@media (min-width: 1200px) {
  .learning__program-link {
    text-align: start;
  }
}
.learning__statistics {
  display: flex;
  flex-direction: column;
  gap: 1.45rem;
  max-width: 460px;
}
@media (min-width: 1200px) {
  .learning__statistics {
    max-width: 390px;
  }
}
.learning__description {
  font-size: 1.15rem;
  text-align: center;
}
.learning__program {
  display: flex;
  justify-content: center;
  gap: 1.45rem;
}
@media (min-width: 1200px) {
  .learning__program {
    gap: 2rem;
  }
}
.learning__stat {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 0.7rem;
}
.learning__stat-number {
  width: 64px;
  height: 64px;
  background-color: var(--accent-color);
  text-align: center;
  line-height: 64px;
  border-radius: 50px;
}
.learning__stat-label {
  font-size: 1.45rem;
  text-transform: uppercase;
