___CSS_LOADER_EXPORT___.push([module.id, `.module-details {
  width: 100%;
  display: none;
  flex-direction: column;
  gap: 1.45rem;
  padding: 20px 20px 34px 20px;
  color: var(--black-color);
  border: 1px solid var(--secondary-color);
}
@media (min-width: 1200px) {
  .module-details {
    display: flex;
  }
}
.module-details--mobile {
  display: flex;
}
@media (min-width: 1200px) {
  .module-details--mobile {
    display: none;
  }
}
.module-details__title {
  display: none;
}
@media (min-width: 640px) {
  .module-details__title {
    display: inline;
  }
}
.module-details__points {
  display: flex;
  flex-flow: row wrap;
  gap: 0.5rem;
}
@media (min-width: 640px) {
  .module-details__points {
    gap: 1.45rem;
  }
}
.module-details__point {
  display: flex;
  align-items: center;
  gap: 1rem;
}
.module-details__point-bullet {
  width: 8px;
  height: 8px;
  background-color: var(--accent-color);
}
.module-details__point-title {
  margin: 0;
}
.module-details__results {
  padding: 20px;
  background-color: var(--secondary-color);
}
.module-details__results-title {
  font-weight: 700;
}
.module-details__results-description {
  margin: 20px 0 0 0;
}
.module-details__button {
  padding: 12px 44px;
  width: 100%;
  background-color: var(--accent-color);
  color: var(--primary-color);
  border-radius: 10px;
  outline: 0;
  border: none;
  cursor: pointer;
  transition: opacity 0.3s ease;
}
.module-details__button:hover {
  opacity: 0.7;
}
.module-details__button:active {
  opacity: 0.5;
}
@media (min-width: 640px) {
  .module-details__button {
    width: max-content;
  }
