  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1.45rem;
  padding: 80px 20px;
  background-color: var(--dark-color);
  border-radius: 32px;
}
@media (min-width: 640px) {
  .installment {
    padding: 80px 94px;
  }
}
@media (min-width: 1200px) {
  .installment {
    flex-direction: row;
    padding: 97px 90px;
    gap: 0;
  }
}
.installment__info {
  text-align: center;
  max-width: 416px;
}
@media (min-width: 1200px) {
  .installment__info {
    text-align: start;
  }
}
.installment__info-title {
  font-size: 2rem;
  margin-bottom: 2.2rem;
}
@media (min-width: 640px) {
  .installment__info-title {
    font-size: 3.5rem;
  }
}
.installment__info-description {
  font-size: 1.45rem;
}
.installment__offer {
  display: flex;
  align-items: center;
  justify-content: center;
  background: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
  background-position: center;
  background-repeat: no-repeat;
  background-size: 100%;
  padding: 30px 0;
  max-width: 313px;
  height: 324px;
  width: 100%;
}
.installment__offer-image {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  min-width: 100%;
  max-height: 100%;
}
@media (min-width: 640px) {
  .installment__offer-image {
    max-width: 100%;
    max-height: max-content;
  }
}
.installment__offer-card {
  padding: 40px 20px;
  border-radius: 12px;
  background: rgba(18, 18, 18, 0.6980392157);
  z-index: 2;
  backdrop-filter: blur(6px);
  text-align: center;
}
.installment__offer-title {
  font-size: 1.45rem;
  max-width: min-content;
  margin: 0 auto;
}
.installment__offer-date {
  color: var(--accent-color);
  font-size: 4.5rem;
}
.installment__payment {
  max-width: 319px;
  width: 100%;
}
.installment__payment-title {
  font-size: 1.45rem;
}
.installment__payment-list {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  padding: 0 0 0 20px;
}
.installment__payment-list > img {
  max-width: max-content;
}
.installment__payment-item {
  font-size: 1rem;
