___CSS_LOADER_EXPORT___.push([module.id, `.form__discount, .form__image-container, .form {
  display: flex;
  align-items: center;
  justify-content: center;
}

.form {
  position: relative;
  flex-direction: column;
  background-color: var(--primary-color);
  border-radius: 20px;
  padding: 60px 20px;
  width: 100%;
}
.form__title {
  font-size: 1.45rem;
  color: var(--black-color);
  max-width: 180px;
  text-align: center;
}
@media (min-width: 640px) {
  .form__title {
    max-width: 236px;
    font-size: 2.2rem;
  }
}
.form__description {
  color: var(--black-color);
  max-width: 236px;
  text-align: center;
}
@media (min-width: 640px) {
  .form__description {
    max-width: 400px;
    font-size: 1.45rem;
  }
}
.form__list {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}
@media (min-width: 640px) {
  .form__list {
    min-width: 400px;
  }
}
.form__phone {
  position: relative;
}
.form__tel {
  position: absolute;
  top: 50%;
  left: 20px;
  transform: translate(0%, -50%);
}
.form__input {
  background-color: var(--input-color);
  border: 1px solid var(--border-color);
  border-radius: 4px;
  padding: 16px 32px;
  width: 100%;
}
.form__input.form__input--tel {
  padding: 16px 32px 16px 70px;
}
.form__input::placeholder {
  color: var(--black-color);
}
.form__button {
  outline: 0;
  border: none;
  margin: 1.1rem 0 0 0;
  color: var(--primary-color);
  border-radius: 10px;
  min-width: 237px;
  min-width: 47px;
  padding: 12px 44px;
  background-color: var(--accent-color);
  transition: opacity 0.3s ease;
  cursor: pointer;
}
.form__button:hover {
  opacity: 0.7;
}
.form__button:active {
  opacity: 0.5;
}
.form__availability {
  text-align: center;
  color: var(--border-color);
}
.form__disclaimer {
  text-align: center;
  font-size: 0.9rem;
  font-weight: 300;
  color: var(--border-color);
  max-width: 319px;
}
.form__image-container {
  position: absolute;
  top: 0;
  right: 0;
}
.form__discount {
  position: absolute;
  top: -40px;
  right: -10px;
}
@media (min-width: 640px) {
  .form__discount {
    top: -40px;
    right: -55px;
  }
}
.form__discount-background {
  position: relative;
  width: 134px;
}
@media (min-width: 640px) {
  .form__discount-background {
    width: 197px;
  }
}
.form__discount-title {
  position: absolute;
  top: 50%;
  right: 50%;
  transform: translate(50%, -50%);
  width: 64px;
}
@media (min-width: 640px) {
  .form__discount-title {
    width: 93px;
  }
