  .menu {
    padding: 46px 82px 0 82px;
  }
}
@media (min-width: 1200px) {
  .menu {
    display: none;
    padding: 46px 105px 0 105px;
    margin: 0 auto;
  }
}
.menu.active {
  transform: translateX(0%);
}
@media (min-width: 1200px) {
  .menu.active {
    display: none;
  }
}
.menu__list {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2rem;
  list-style: none;
  margin: 0;
  padding: 0;
}
.menu__link {
  position: relative;
  color: var(--primary-color);
  text-decoration: none;
  font-size: 1.6rem;
  padding: 10px 0;
  opacity: 0.8;
  transition: opacity 0.3s eases;
}
.menu__link:hover {
  opacity: 1;
}
