___CSS_LOADER_EXPORT___.push([module.id, `.result-summary {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1.65rem;
}
.result-summary > h2, .result-summary p {
  margin: 0;
}
@media (min-width: 640px) {
  .result-summary {
    gap: 2.8rem;
  }
}
.result-summary__title {
  font-size: 2rem;
  text-align: center;
}
@media (min-width: 640px) {
  .result-summary__title {
    font-size: 4rem;
  }
}
@media (min-width: 1200px) {
  .result-summary__title {
    font-size: 3.5rem;
  }
}
.result-summary__description {
  font-size: 1.1rem;
  text-align: center;
}
@media (min-width: 640px) {
  .result-summary__description {
    max-width: 480px;
    font-size: 1.45rem;
  }
}
.result-summary__container {
  background-color: var(--dark-color);
  padding: 30px 20px;
  width: 100%;
}
.result-summary__list {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  width: 100%;
}
@media (min-width: 1200px) {
  .result-summary__list {
    flex-direction: row;
    justify-content: center;
    gap: 3rem;
  }
}
.result-summary__statistic {
  display: flex;
  min-height: 24px;
}
.result-summary__practise-percent {
  position: relative;
  flex-basis: 49.99%;
  background-color: var(--accent-color);
}
.result-summary__practise-percent::after {
  position: absolute;
  left: 100%;
  bottom: 50%;
  content: "";
  width: 44px;
  border: 2px solid var(--accent-color);
  transform: translateX(-50%) rotate(-90deg);
}
.result-summary__teorogy-percent {
  position: relative;
  flex-basis: 33.33%;
  background-color: var(--pink-color);
}
.result-summary__teorogy-percent::after {
  position: absolute;
  left: 100%;
  bottom: 50%;
  content: "";
  width: 44px;
  border: 2px solid var(--pink-color);
  transform: translateX(-50%) rotate(-90deg);
}
.result-summary__feedback-percent {
  flex-basis: 16.66%;
  background-color: var(--primary-color);
