  display: flex;
  align-items: center;
  justify-content: center;
}

.module__container {
  display: flex;
  align-items: center;
  padding: 32px 16px;
  gap: 1.1rem;
  cursor: pointer;
}
.module__container--active {
  background-color: var(--active-color);
  font-weight: 700;
}
.module__container--active > .module__title {
  font-weight: 700;
}
.module__point {
  background-color: var(--accent-color);
  font-size: 1rem;
  min-width: 24px;
  height: 24px;
  border-radius: 50px;
  margin: 0;
}
.module__title {
  font-size: 1.1rem;
  font-weight: 400;
  color: var(--black-color);
}
@media (min-width: 640px) {
  .module__title {
    font-size: 1.3rem;
  }
