___CSS_LOADER_EXPORT___.push([module.id, `.ai-usage__title {
  font-size: 2rem;
  text-align: center;
  max-width: 600px;
  margin: 0 auto 2rem auto;
}
@media (min-width: 640px) {
  .ai-usage__title {
    font-size: 3.5rem;
    max-width: 100%;
  }
}
.ai-usage__intro {
  font-size: 1.3rem;
  margin: 0 0 4rem 0;
  text-align: center;
  max-width: 600px;
  margin: 0 auto 4rem auto;
}
.ai-usage__cards {
  display: grid;
  grid-template-columns: 1fr;
  gap: 20px;
}
@media (min-width: 1200px) {
  .ai-usage__cards {
    grid-template-columns: repeat(2, 1fr);
  }
}
.ai-usage__card {
  display: flex;
  align-items: flex-start;
  gap: 1.3rem;
  background: radial-gradient(68.63% 463.3% at 100% 100%, #504740 0%, #5C4359 16.36%, #382B3F 42.19%, #171717 100%);
  border-radius: 20px;
  padding: 40px 20px;
}
@media (min-width: 640px) {
  .ai-usage__card {
    gap: 2.8rem;
    padding: 40px;
  }
}
.ai-usage__info {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.ai-usage__heading {
  font-size: 1.3rem;
}
@media (min-width: 640px) {
  .ai-usage__heading {
    font-size: 1.8rem;
  }
}
.ai-usage__conclusion {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 40px 0;
  min-height: 200px;
}
.ai-usage__conclusion-image {
  min-width: 236px;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}
@media (min-width: 1200px) {
  .ai-usage__conclusion-image {
    min-width: 180px;
  }
}
.ai-usage__conclusion-title {
  font-size: 1.15rem;
  font-weight: 400;
  text-align: center;
  z-index: 1;
}
@media (min-width: 640px) {
  .ai-usage__conclusion-title {
    font-size: 1.7rem;
  }
