  display: flex;
  align-items: center;
  justify-content: center;
}

.course-form {
  display: flex;
  flex-direction: column;
  gap: 100px;
}
@media (min-width: 1200px) {
  .course-form {
    flex-direction: row;
  }
}
.course-form__container {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.course-form__information {
  gap: 1rem;
}
@media (min-width: 1200px) {
  .course-form__information {
    justify-content: flex-start;
  }
}
.course-form__title {
  font-size: 2.6rem;
  text-align: center;
  margin-top: 1.45rem;
  text-transform: uppercase;
}
@media (min-width: 640px) {
  .course-form__title {
    font-size: 3.1rem;
  }
}
@media (min-width: 1200px) {
  .course-form__title {
    text-align: start;
  }
}
.course-form__intro {
  font-weight: 700;
  font-size: 1.45rem;
  text-align: center;
  margin-top: 1.1rem;
}
