  .course-form__intro {
    font-size: 1.8rem;
  }
}
@media (min-width: 1200px) {
  .course-form__intro {
    text-align: start;
  }
}
.course-form__description {
  font-size: 1.45rem;
  text-align: center;
  margin-top: 3.5rem;
}
@media (min-width: 1200px) {
  .course-form__description {
    text-align: start;
  }
}
.course-form__price {
  font-size: 1.1rem;
  margin-top: 1rem;
  text-align: center;
}
@media (min-width: 1200px) {
  .course-form__price {
    text-align: start;
  }
}
.course-form__timers {
  display: flex;
  justify-content: center;
  gap: 1rem;
}
@media (min-width: 1200px) {
  .course-form__timers {
    justify-content: flex-start;
  }
}
.course-form__timer {
  flex-direction: column;
  background-color: var(--input-color);
  color: var(--black-color);
  padding: 12px 20px;
  border-radius: 10px;
  width: 60px;
  max-height: 64px;
}
@media (min-width: 640px) {
  .course-form__timer {
    width: 88px;
  }
}
.course-form__time {
  font-weight: 700;
}
@media (min-width: 640px) {
  .course-form__time {
    font-size: 1.45rem;
  }
}
.course-form__label {
  font-size: 0.9rem;
