___CSS_LOADER_EXPORT___.push([module.id, `.footer__info {
  display: flex;
  align-items: center;
  justify-content: center;
}

.footer {
  padding: 100px 0 40px 0;
}
@media (min-width: 640px) {
  .footer {
    padding: 120px 0 40px 0;
  }
}
.footer__container {
  display: grid;
  grid-template-columns: 1fr;
  gap: 2.2rem;
  justify-items: center;
}
@media (min-width: 1200px) {
  .footer__container {
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: 1fr;
  }
}
.footer__info {
  flex-direction: column;
  gap: 2.2rem;
}
@media (min-width: 1200px) {
  .footer__info {
    grid-area: 1/1/2/2;
    align-items: flex-start;
  }
}
.footer__logo {
  width: 84px;
  height: 34px;
}
.footer__address {
  text-align: center;
}
@media (min-width: 1200px) {
  .footer__address {
    text-align: start;
  }
}
.footer__social-media {
  display: flex;
  gap: 1.45rem;
}
.footer__icon {
  width: 24px;
  height: 24px;
}
.footer__about {
  display: none;
  flex-direction: column;
  gap: 2.2rem;
  max-width: 285px;
}
@media (min-width: 1200px) {
  .footer__about {
    display: flex;
    grid-area: 1/2/2/3;
  }
}
.footer__about-title {
  font-size: 1.3rem;
}
.footer__about-list {
  display: flex;
  gap: 2.2rem;
}
.footer__about-container {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}
.footer__about-link {
  color: var(--primary-color);
  text-decoration: none;
  cursor: pointer;
}
.footer__contacts {
  display: flex;
  flex-direction: column;
  max-width: 294px;
  gap: 1rem;
}
@media (min-width: 1200px) {
  .footer__contacts {
    grid-area: 1/3/2/-1;
  }
}
.footer__contacts-title {
  display: none;
  font-size: 1.45rem;
  font-weight: 700;
  margin: 0;
}
@media (min-width: 1200px) {
  .footer__contacts-title {
    display: inline;
  }
}
.footer__contact {
  display: flex;
  gap: 0.5rem;
}
.footer__contact-icon {
  width: 24px;
  height: 24px;
}
.footer__contact-text {
  color: var(--primary-color);
}
.footer__rights {
  display: flex;
  flex-direction: column;
  width: 100%;
  padding: 40px 18px;
  gap: 1.75rem;
  border-top: 1px solid var(--elements-color);
}
@media (min-width: 1200px) {
  .footer__rights {
    grid-area: 3/1/-1/-1;
    flex-direction: row;
    padding: 40px 0;
  }
}
.footer__rights-title {
  font-weight: 700;
  margin-bottom: 1.75rem;
  text-align: center;
  margin: 0;
}
@media (min-width: 1200px) {
  .footer__rights-title {
    text-align: start;
    font-size: 0.8rem;
  }
}
.footer__rights-list {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  gap: 1.75rem;
}
@media (min-width: 1200px) {
  .footer__rights-list {
    flex-direction: row;
    justify-content: space-between;
  }
}
.footer__rights-link {
  text-align: center;
  color: var(--rights-color);
  text-decoration: none;
}
@media (min-width: 1200px) {
  .footer__rights-link {
    font-size: 0.8rem;
    text-align: start;
  }
