___CSS_LOADER_EXPORT___.push([module.id, `.studying-card {
  display: flex;
  justify-content: flex-start;
  gap: 1rem;
}
.studying-card h4, .studying-card p {
  margin: 0;
}
@media (min-width: 640px) {
  .studying-card {
    align-items: center;
  }
}
@media (min-width: 1200px) {
  .studying-card {
    flex-direction: column;
  }
  .studying-card h4, .studying-card p {
    text-align: center;
  }
}
.studying-card__icon {
  position: relative;
  width: 44px;
  height: 44px;
  z-index: 2;
}
.studying-card__icon::after {
  content: "";
  position: absolute;
  bottom: 0;
  right: 0;
  width: 10px;
  height: 10px;
  background-color: var(--dark-color);
  color: var(--primary-color);
  font-size: 1rem;
  border-radius: 50px;
  z-index: 3;
}
@media (min-width: 640px) {
  .studying-card__icon {
    width: 130px;
    height: 120px;
  }
}
.studying-card__content {
  display: flex;
  flex-direction: column;
  gap: 0.8rem;
  max-width: 375px;
}
@media (min-width: 1200px) {
  .studying-card__content {
    align-items: center;
  }
}
.studying-card__title {
  font-size: 1.1rem;
}
.studying-card__description {
  font-size: 0.9rem;
