  display: flex;
  flex-direction: column;
  align-items: center;
}
.ai-applications > h2, .ai-applications > p {
  text-align: center;
  max-width: 500px;
}
@media (min-width: 1200px) {
  .ai-applications > h2, .ai-applications > p {
    max-width: 880px;
  }
}
.ai-applications__title {
  font-size: 2rem;
}
@media (min-width: 640px) {
  .ai-applications__title {
    font-size: 4rem;
  }
}
.ai-applications__description {
  font-size: 1.1rem;
}
@media (min-width: 640px) {
  .ai-applications__description {
    font-size: 1.45rem;
  }
}
.ai-applications__list {
  display: flex;
  flex-direction: column;
  gap: 1.45rem;
  margin: 2.8rem 0;
}
@media (min-width: 1200px) {
  .ai-applications__list {
    flex-direction: row;
  }
}
.ai-applications__conclusion {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 221px;
}
.ai-applications__conclusion-image {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  max-width: 160px;
  max-height: 221px;
}
.ai-applications__conclusion-title {
  text-align: center;
  z-index: 2;
}
@media (min-width: 640px) {
  .ai-applications__conclusion-title {
    font-size: 1.45rem;
    max-width: 556px;
  }
}
@media (min-width: 1200px) {
  .ai-applications__conclusion-title {
    max-width: 895px;
  }
