  display: flex;
  flex-direction: column;
  gap: 2rem;
  padding: 60px 24px;
  border-radius: 32px;
  background-color: var(--card-color);
  background: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
  background-size: cover;
  background-repeat: no-repeat;
  background-position: bottom;
  box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.2509803922);
}
@media (min-width: 640px) {
  .course {
    padding: 80px 52px;
    gap: 3.5rem;
  }
}
@media (min-width: 1200px) {
  .course {
    padding: 80px 104px;
  }
}
.course__title {
  font-size: 2rem;
  text-align: center;
}
@media (min-width: 640px) {
  .course__title {
    font-size: 4rem;
  }
}
.course__group {
  display: grid;
  grid-template-columns: 1fr;
  gap: 1.7rem;
}
@media (min-width: 640px) {
  .course__group {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 1200px) {
  .course__group {
    grid-template-columns: repeat(4, 1fr);
  }
}
.course__group-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 1.7rem;
  padding: 60px 38px;
  background-color: var(--card-color);
  border-radius: 32px;
}
@media (min-width: 640px) {
  .course__group-container {
    padding: 60px 16px;
  }
}
@media (min-width: 1200px) {
  .course__group-container {
    padding: 40px 18px;
  }
}
.course__group-icon {
  width: 48px;
  height: 48px;
}
.course__group-title {
  font-size: 1.1rem;
  text-align: center;
}
.course__group-description {
  text-align: center;
