  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0 20px;
  overflow: hidden;
  gap: 2.5rem;
}
@media (min-width: 640px) {
  .feedbacks {
    gap: 4rem;
  }
}
.feedbacks__image {
  width: 100%;
}
.feedbacks__title {
  font-size: 2rem;
  text-align: center;
}
@media (min-width: 640px) {
  .feedbacks__title {
    font-size: 3.4rem;
    max-width: 604px;
  }
}
@media (min-width: 1200px) {
  .feedbacks__title {
    text-align: start;
    max-width: 689px;
  }
}
.feedbacks__slider {
  overflow: hidden;
}
.feedbacks__slider__image {
  width: 100%;
  max-height: max-content;
}
.feedbacks__button {
  outline: 0;
  border: none;
  background-color: var(--accent-color);
  color: var(--primary-color);
  font-weight: 700;
  border-radius: 10px;
  padding: 12px 44px;
  transition: opacity 0.3s ease;
}
.feedbacks__button:hover {
  opacity: 0.7;
}
.feedbacks__button:active {
  opacity: 0.5;
}

.swiper {
  width: 100%;
}
.swiper-container-second {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  height: 100%;
}
.swiper-wrapper-second {
  display: flex;
  width: 100%;
}
.swiper-button-prev {
  display: none;
}
@media (min-width: 1200px) {
  .swiper-button-prev {
    display: block;
  }
}
.swiper-button-prev::after {
  content: "";
}
.swiper-button-prev-second {
  width: 44px;
  height: 24px;
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
}
.swiper-button-next {
  display: none;
}
@media (min-width: 1200px) {
  .swiper-button-next {
    display: block;
  }
}
.swiper-button-next::after {
  content: "";
}
.swiper-button-next-second {
  width: 44px;
  height: 24px;
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
  transform: rotate(180deg);
}
.swiper-slide-second {
  overflow: hidden;
  transition: opacity 0.2s ease, transform 0.2s ease;
}
@media (min-width: 640px) {
  .swiper-slide-second {
    max-width: 291px;
  }
}
@media (min-width: 1200px) {
  .swiper-slide-second {
    max-width: 395px;
    width: 395px;
    opacity: 0.5;
  }
}
@media (min-width: 1200px) {
  .swiper-slide-active {
    opacity: 1;
  }
}
.swiper-pagination {
  position: static;
  text-align: center;
  margin-top: 10px;
  color: var(--accent-color);
}
.swiper-pagination-bullet {
  background-color: var(--pagination-color);
}
.swiper-pagination-bullet-bullet-active {
  background-color: var(--pink-color);
}

@media (min-width: 1200px) {
  .swiper-container-second .swiper-slide {
    opacity: 0.5;
    transform: scale(0.85);
  }
}

@media (min-width: 1200px) {
  .swiper-container-second .swiper-slide-active {
    opacity: 1;
    transform: scale(1);
  }
