___CSS_LOADER_EXPORT___.push([module.id, `.header {
  position: sticky;
  top: 0;
  background-color: var(--bg-color);
  z-index: 100;
}
.header__container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 20px 0;
}
@media (min-width: 1400px) {
  .header__container {
    max-width: 1400px;
  }
}
.header__logo {
  width: 64px;
}
@media (min-width: 640px) {
  .header__logo {
    width: 84px;
  }
}
.header__button {
  min-width: 168px;
  min-height: 42px;
  font-weight: 700;
  border-radius: 10px;
  padding: 12px 44px;
  border: 1px solid var(--primary-color);
  color: var(--primary-color);
  background-color: transparent;
  outline: 0;
  cursor: pointer;
  transition: background-color 0.3s ease, color 0.3s ease;
}
.header__button:hover {
  background-color: var(--primary-color);
  color: var(--bg-color);
}
.header__button--tablet {
  display: none;
}
@media (min-width: 640px) {
  .header__button--tablet {
    display: block;
  }
}
@media (min-width: 1200px) {
  .header__button--tablet {
    display: none;
  }
}
.header__button--mobile {
  display: block;
}
@media (min-width: 640px) {
  .header__button--mobile {
    display: none;
  }
}
.header__button--desktop {
  display: none;
}
@media (min-width: 1200px) {
  .header__button--desktop {
    display: block;
  }
}

.nav {
  display: none;
}
@media (min-width: 1200px) {
  .nav {
    display: block;
  }
}
.nav__list {
  display: flex;
  gap: 2.8rem;
  padding: 0;
  list-style: none;
}
.nav__link {
  text-decoration: none;
  color: var(--primary-color);
